import Web3 from "web3";
import consensus_abi from "../abis/consensus_abi.json";
import block_reward_abi from "../abis/block_reward_abi.json";
import {makeBatchRequest} from "../utils/promisify";
import {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {
    Alert,
    Box,
    Button, CircularProgress,
    Container, Dialog,
    FormControl,
    Grid, InputAdornment,
    List,
    ListItem,
    ListItemText,
    Paper, Slide, Snackbar, Tab, Tabs,
    TextField,
    Typography
} from "@mui/material";
import BigNumber from "bignumber.js";
import {updateWalletBalance} from "../redux/slices/coineusSlice";

const coineusValidatorAddress = "0x53e2033F86C1a54bEbAE893DC8C9fAe179b3Faa2";
const consensusAddress = "0x3014ca10b91cb3D0AD85fEf7A3Cb95BCAc9c0f79";

const blockRewardAddress = "0x63D4efeD2e3dA070247bea3073BCaB896dFF6C9B";
const web3 = new Web3('https://rpc.fuse.io');


const consensusContract = new web3.eth.Contract(consensus_abi, consensusAddress);
const blockRewardsContract = new web3.eth.Contract(block_reward_abi, blockRewardAddress);

const calcRewardPerYourBlocks = (
    rewardPerBlock,
    stakeAmount,
    numberOfValidators,
    totalStakeAmount,
    fee) => new BigNumber(rewardPerBlock)
    .multipliedBy(new BigNumber(stakeAmount))
    .multipliedBy(numberOfValidators)
    .div(new BigNumber(totalStakeAmount))
    .multipliedBy((1 - fee))


export default function Staking() {

    const dispatch = useDispatch();
    const {wallet} = useSelector(state => state.coineus);


    function TransitionUp(props) {
        return <Slide {...props} direction="up" />;
    }

    const [snackbarOpen, setSnackbarOpen] = useState(false);
    const [snackbarMessage, setSnackbarMessage] = useState("Testing Snackbar");
    const [snackbarSeverity, setSnackbarSeverity] = useState("success");
    const [snackbarTransition, setSnackbarTransition] = useState(undefined);

    const [pending, setPending] = useState(false);
    const [amount, setAmount] = useState("");
    const [data, setData] = useState({});
    const [connectedContract, setConnectedContract] = useState(0);
    const [action, setAction] = useState('stake');

    const getConsensus = () => {

        const calls = [
            consensusContract.methods.delegators(coineusValidatorAddress).call,
            consensusContract.methods.totalStakeAmount().call,
            consensusContract.methods.stakeAmount(coineusValidatorAddress).call,
            consensusContract.methods.currentValidatorsLength().call,
            consensusContract.methods.validatorFee(coineusValidatorAddress).call,
            blockRewardsContract.methods.getBlocksPerYear().call,
            blockRewardsContract.methods.getBlockRewardAmount().call
        ];

        if (wallet.address) {
            calls.push(consensusContract.methods.delegatedAmount(wallet.address, coineusValidatorAddress).call)
            const web3 = new Web3(window.provider);
            setConnectedContract(new web3.eth.Contract(consensus_abi, consensusAddress));

            web3.eth.getBalance(wallet.address).then(resp => {
                dispatch(updateWalletBalance(resp / (10 ** 18)));
            })
        } else {
            dispatch(updateWalletBalance(0));
        }

        makeBatchRequest(web3, calls).then(resp => {
            const [
                delegators,
                total_stake_amount,
                amount_delegated,
                num_validators,
                validator_fee,
                blocks_per_year,
                reward_per_block
            ] = resp;

            let staked_amount = 0;

            if (wallet.address) {
                staked_amount = resp[resp.length - 1];
            }

            console.log({
                delegators,
                total_stake_amount,
                amount_delegated,
                staked_amount,
                num_validators,
                validator_fee,
                blocks_per_year,
                reward_per_block,
                fuse_balance: wallet
            })

            setData({
                delegators,
                total_stake_amount,
                amount_delegated: amount_delegated / (10 ** 18),
                staked_amount,
                num_validators,
                validator_fee,
                blocks_per_year,
                reward_per_block,
                fuse_balance: wallet
            })

        })

    }

    const rewardPerYourBlocks = calcRewardPerYourBlocks(
        data.reward_per_block || 0,
        amount * (10 ** 18) || 0,
        data.num_validators || 0,
        data.total_stake_amount || 0,
        data.validator_fee / (10 ** 18) || 0
    )

    const average = rewardPerYourBlocks.div(data.num_validators || 0)
    const rewardPerYear = average.multipliedBy(data.blocks_per_year || 0)
    const _rewardPerYourBlocks = calcRewardPerYourBlocks(
        data.reward_per_block || 0,
        (10 ** 18),
        data.num_validators || 0,
        data.total_stake_amount || 0,
        data.validator_fee / (10 ** 18) || 0
    )
    const _average = _rewardPerYourBlocks.div(data.num_validators || 0)
    const _rewardPerYear = _average.multipliedBy(data.blocks_per_year || 0)
    const _estimatedAPR = _rewardPerYear.div((10 ** 18))

    useEffect(getConsensus, []);

    useEffect(() => {
        if (wallet.address) {
            getConsensus();
        } else {
            getConsensus();
        }
    }, [wallet.address]);

    const delegate = () => {
        const amt = new BigNumber(amount * (10 ** 18));
        connectedContract.methods
            .delegate(coineusValidatorAddress)
            .send({
                from: wallet.address,
                value: amt
            })
            .once('transactionHash', function (tx) {
                setPending(true)
            })
            .on('error', function (error) {
                setPending(false)
                handleError(error)
            })
            .then(function (receipt) {
                getConsensus();
                setPending(false);
                setAmount('');
            })
            .catch(()=>{});

    }

    const withdraw = () => {
        try {
            const amt = new BigNumber(amount * (10 ** 18));
            connectedContract.methods
                .withdraw(coineusValidatorAddress, amt)
                .send({
                    from: wallet.address
                })
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                    handleError(error)
                })
                .then(function (receipt) {
                    getConsensus();
                    setPending(false);
                    setAmount('');
                })
                .catch(()=>{});
        } catch (e) {
            handleError({message:e.toString()})
        }
    }

    const handleError = (error) => {
        if(error.message){
            setSnackbarTransition(() => TransitionUp)
            setSnackbarSeverity("error");
            setSnackbarMessage(error.message);
            setSnackbarOpen(true);
        }
    }

    return (
        <Container maxWidth="sm">
            <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>

                <Grid item xs={12}>
                    <Paper style={{padding: 20}}>
                        <Typography variant="h4">{data.amount_delegated?.toLocaleString()}</Typography>
                        <Typography variant="caption">Total FUSE Staked with Coineus</Typography>
                    </Paper>
                </Grid>

                <Grid item xs={7}>
                    <Paper style={{padding: 20, color: '#000', backgroundColor: '#fbca00'}}>
                        <Typography
                            variant="h5">{data.staked_amount ? (data.staked_amount / (10**18)).toLocaleString() : '--'}</Typography>
                        <Typography variant="caption">Your FUSE Staked</Typography>
                    </Paper>
                </Grid>

                <Grid item xs={5}>
                    <Paper style={{padding: 20, color: '#000', backgroundColor: '#fbca00'}}>
                        <Typography
                            variant="h5">{`${isNaN(_estimatedAPR) ? "--" : (_estimatedAPR * 100).toFixed(2)} %`}</Typography>
                        <Typography variant="caption">Estimated APR</Typography>
                    </Paper>
                </Grid>

                <Grid item xs={12}>
                    <Paper>
                        <Tabs
                            variant="fullWidth"
                            value={action}
                            onChange={(ev, val) => {
                                setAction(val)
                                setAmount('')
                            }}
                        >
                            <Tab id="stake" value="stake" label="STAKE"/>
                            <Tab id="unstake" value="unstake" label="UNSTAKE"/>
                        </Tabs>
                        {
                            action === "stake" && <Box style={{padding: 20}}>
                                <List>
                                    <ListItem disableGutters disablePadding>
                                        <ListItemText
                                            secondary="FUSE Balance"
                                        />
                                        <ListItemText
                                            secondary={`${(wallet.balance || "--").toLocaleString('en-US', {
                                                minimumFractionDigits: 2,
                                                maximumFractionDigits: 8
                                            })} FUSE`}
                                            secondaryTypographyProps={{textAlign: 'right'}}
                                        />
                                    </ListItem>
                                </List>
                                <FormControl fullWidth style={{marginBottom: 20}}>
                                    <TextField
                                        autoComplete="off"
                                        type="number"
                                        value={amount}
                                        placeholder="Enter Amount"
                                        onChange={(ev) => {
                                            if(ev.target.value > 5000000)return;
                                            setAmount(ev.target.value)
                                        }}
                                        InputProps={{
                                            endAdornment: <InputAdornment position="end">
                                                <Button variant="contained" onClick={() => {
                                                    setAmount(wallet.balance - .1)
                                                }
                                                }>MAX</Button>
                                            </InputAdornment>,
                                        }}
                                    />
                                </FormControl>
                                <Button fullWidth variant="contained" disabled={!amount || amount > 5000000} onClick={delegate}>Stake</Button>
                                <List style={{marginTop: 20}}>
                                    <ListItem disableGutters disablePadding>
                                        <ListItemText
                                            secondary="Est. Rewards Per Block"
                                        />
                                        <ListItemText
                                            secondary={`${(rewardPerYourBlocks / (10 ** 18)).toLocaleString('en-US', {
                                                minimumFractionDigits: 2,
                                                maximumFractionDigits: 8
                                            })} FUSE`}
                                            secondaryTypographyProps={{textAlign: 'right'}}
                                        />
                                    </ListItem>
                                    <ListItem disableGutters disablePadding>
                                        <ListItemText
                                            secondary="Est. Rewards Per Year"
                                        />
                                        <ListItemText
                                            secondary={`${(rewardPerYear / (10 ** 18)).toLocaleString('en-US', {
                                                minimumFractionDigits: 2,
                                                maximumFractionDigits: 8
                                            })} FUSE`}
                                            secondaryTypographyProps={{textAlign: 'right'}}
                                        />
                                    </ListItem>
                                </List>
                            </Box>
                        }

                        {
                            action === "unstake" && <Box style={{padding: 20}}>
                                <List>
                                    <ListItem disableGutters disablePadding>
                                        <ListItemText
                                            secondary="Staked FUSE"
                                        />
                                        <ListItemText
                                            secondary={`${(data.staked_amount / (10**18)|| "--").toLocaleString('en-US', {
                                                minimumFractionDigits: 2,
                                                maximumFractionDigits: 8
                                            })} FUSE`}
                                            secondaryTypographyProps={{textAlign: 'right'}}
                                        />
                                    </ListItem>
                                </List>
                                <FormControl fullWidth style={{marginBottom: 20}}>
                                    <TextField
                                        autoComplete="off"
                                        type="number"
                                        value={amount}
                                        placeholder="Enter Amount"
                                        onChange={(ev) => {
                                            if(ev.target.value > data.staked_amount / (10**18))return;
                                            setAmount(ev.target.value)
                                        }}
                                        InputProps={{
                                            endAdornment: <InputAdornment position="end">
                                                <Button variant="contained" onClick={() => {
                                                    setAmount(data.staked_amount / (10**18))
                                                }
                                                }>MAX</Button>
                                            </InputAdornment>,
                                        }}
                                    />
                                </FormControl>
                                <Button fullWidth variant="contained" disabled={!amount || amount > data.staked_amount / (10**18)} onClick={withdraw}>Unstake</Button>
                            </Box>
                        }

                    </Paper>
                </Grid>

            </Grid>

            <Dialog open={pending} anchorOrigin={{ vertical: 'center', horizontal: 'center' }}>
                <Paper style={{padding:20}}>
                    <CircularProgress size={20}/>
                    <Typography variant='caption' style={{
                        verticalAlign: 'top',
                        marginLeft: 10
                    }}>Transaction Pending</Typography>
                </Paper>
            </Dialog>

            <Snackbar
                open={snackbarOpen}
                anchorOrigin={{vertical: "bottom", horizontal: "center"}}
                autoHideDuration={10000}
                onClose={()=>{
                    setSnackbarOpen(false);
                }}
                TransitionComponent={snackbarTransition}
            >
                <Alert
                    severity={snackbarSeverity}
                    variant="filled"
                    elevation={6}
                >
                    <Typography>{snackbarMessage}</Typography>
                </Alert>
            </Snackbar>

        </Container>
    )
}